  padding: 2rem 1rem;
  text-transform: uppercase;
  font-family: "Franklin Gothic Medium";
  color: #000000;
  font-size: 1.875rem;
  font-weight: 400;
  letter-spacing: 0.2rem;
  line-height: 1;
  text-align: center;
}

#products .container .products-header p {
  font-family: Cambria;
  color: rgba(89, 89, 89, 1);
